.div {
  position: relative;
  line-height: 142%;
  font-weight: 500;
}
.wrapper {
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs);
  align-items: center;
  justify-content: center;
}
.canIChangeMyPlanLaterWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: var(--font-size-xl);
  color: var(--color-black);
}
.closeButtonIcon {
  position: relative;
  width: 53px;
  height: 53px;
}
.frameParent {
  position: absolute;
  height: 13.94%;
  width: 47.42%;
  top: 53.14%;
  right: 52.58%;
  bottom: 32.92%;
  left: 0;
  border-radius: 0 0 var(--br-3xs) var(--br-3xs);
  background-color: var(--color-whitesmoke);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-3xl) 0 var(--padding-13xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-50xl);
  opacity: 0;
  text-align: left;
  font-size: var(--font-size-21xl);
  color: var(--color-darkorange-100);
  font-family: var(--font-poppins);
}
