.getInTouch {
  position: relative;
}
.getInTouchWrapper {
  position: absolute;
  top: calc(50% - 27px);
  left: 1643px;
  border-radius: var(--br-36xl);
  background: linear-gradient(90.82deg, #000, #282828);
  box-shadow: 0 3px 20px rgba(0, 0, 0, 0.34);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-lgi) var(--padding-7xl);
  align-items: center;
  justify-content: center;
}
.home {
  position: relative;
  font-weight: 600;
}
.frameParent,
.homeWrapper {
  display: flex;
  flex-direction: row;
}
.homeWrapper {
  padding: var(--padding-8xs) var(--padding-3xs);
  align-items: center;
  justify-content: center;
}
.frameParent {
  position: absolute;
  top: calc(50% - 17px);
  left: calc(50% + 41px);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-25xl);
  color: var(--color-black);
}
.finalLogo1 {
  position: absolute;
  top: 13px;
  left: 95px;
  width: 80px;
  height: 80px;
  object-fit: cover;
}
.property1default {
  position: relative;
  background-color: var(--white);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.25);
  width: 1920px;
  height: 100px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-2xl);
  color: var(--white);
  font-family: var(--font-inter);
}
